import React from 'react';
import './Projects.css';
import Project from './Project';
function Projects() {
    let i=0;
    const data = [
        {
            "heading": "Employee Income Tax Management System",
            "type": "Project",
            "technology": [
                "Java (core)",
                "MySQL"
            ],
            "description": "This Project is all about managing income-tax records and calculating income-tax for every employee"
        },
        {
            "heading": "Fan automation Using Arduino",
            "type": "Project",
            "technology": [
                "IoT"
            ],
            "description": "This was a fun project, my team got selected for national level IUCEE challenge 2017, the topic was 'Home Automation'. In this project, we used arduino to automate a fan using temperature input from temperature and humidity sensor, DHT11, and also uploaded the data online on thingspeak server using wifi module, ESP8266"
        },
        {
            "heading": "Smart Car",
            "type": "Project",
            "technology": [
                "IoT"
            ],
            "description": "Another project that I took for fun, automating a car sounds cool right? we added automatic headlights, automatic emergency breaks, and good looks to it. We also tried adding auto-overtake system but... let's say it need a bit more efforts to get that working. We used Arduino Uno board, ultrasonic sensors, infrared sensors and a big toy car chassis for the project"
        },
        {
            "heading": "Skyfi Labs IoT Workshop",
            "type": "Workshop",
            "technology": [
                "IoT"
            ],
            "description": "Attended a 2 Day workshop organised by Skyfi Labs and completed with distinction and qualified for National level IUCEE challenge 2017"
        },
        {
            "heading": "i3indya Technologies IoT Workshop",
            "type": "Workshop",
            "technology": [
                "IoT"
            ],
            "description": "Attended a workshop organised by i3indya"
        },
        {
            "heading": "Java (Advanced) training from AMP Solutions Ltd.",
            "type": "Training",
            "technology": [
                "Java (Advanced)"
            ],
            "description": "Attended a 2 Month Java (Advanced) training organised by AMP Solutions Limited. The training helped me understand the concepts of Servlets and jsp."
        },
        {
            "heading": "Online Job Search Application",
            "type": "Project",
            "technology": [
                "Java (Advanced)",
                "HTML",
                "CSS"
            ],
            "description": "Created a Job search application that can help job searchers to search job online and help employers advertise there vacancies"
        }
    
    ];
    return (
        <div className="Projects">
            <h1 className="Projects-heading">My Work</h1>
            <div className="Projects-box">
                {data.map((project)=> (
                    <Project key={i++} heading={project.heading} type={project.type} technology={project.technology} description={project.description}/>
                ))}
            </div>
       </div>
    )
}

export default Projects
