import React from 'react'
import './Footer.css'
import MailIcon from '@material-ui/icons/Mail';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import Button from '@material-ui/core/Button';
function Footer() {
    return (
        <div className="footer">
            <div className="contacts">
                <div className="l">
                    <MailIcon className="mailIcon" fontSize="large"/>
                    <span className="mail">sahilkholia9@gmail.com</span>
                </div>
            </div>
            <div className="social">
                <Button className="list">
                    <a href="https://www.linkedin.com/in/sahil-kholia" target="_blank">
                        <LinkedInIcon fontSize="large"/>
                    </a>
                </Button>
                <Button className="list">
                    <a href="https://www.facebook.com/sahil.kholia.1" target="_blank">
                        <FacebookIcon fontSize="large"/>
                    </a>
                </Button>
                <Button className="list">
                    <a href="https://www.instagram.com/sahil_kholia/" target="_blank">
                        <InstagramIcon fontSize="large"/>
                    </a>
                </Button>
            </div>
        </div>
    )
}

export default Footer
