import React from 'react'
import './Timeline.css'


function Timeline() {
    return (
        <div className="timeline">
            <h1 className="timeline-heading">Journey</h1>
            <div className="timeline-entry">
                <div className="timeline-entry-left">
                    <span className="timeline-entry-date">
                        9 May 1998
                    </span>
                </div>
                <div className="timeline-entry-right">
                    <p className="timeline-entry-para">
                        They told me I was born on this day. THEY told me... I don't remember.
                    </p>
                </div>
            </div>


            <div className="timeline-entry">
                <div className="timeline-entry-left">
                    <span className="timeline-entry-date">
                        April 2016
                    </span>
                    <span className="timeline-entry-horizontal-line"></span>
                </div>
                <div className="timeline-entry-right">
                    <p className="timeline-entry-para">
                    Completed my class 12th from Army Public School Yol Cantt. with 69% in Non-Medical. If you’re smiling on that percentage, you’re going to hell. Now, this is not the only school I went to but that’s a story for some other time.
                    </p>
                </div>
            </div>


            <div className="timeline-entry">
                <div className="timeline-entry-left">
                    <span className="timeline-entry-date">
                        August 2016
                    </span>
                    <span className="timeline-entry-horizontal-line"></span>
                </div>
                <div className="timeline-entry-right">
                    <p className="timeline-entry-para">
                        Started my Bachelor of Engineering in Computer Science and Engineering at Chandigarh University and the new life begins.
                    </p>
                </div>
            </div>

            <div className="timeline-entry">
                <div className="timeline-entry-left">
                    <span className="timeline-entry-date">
                        July 2020
                    </span>
                    <span className="timeline-entry-horizontal-line"></span>
                </div>
                <div className="timeline-entry-right">
                    <p className="timeline-entry-para">
                        Completed my Bachelor of Engineering in Computer Science and Engineering with my last college days spent in quarantine.
                    </p>
                </div>
            </div>

            <div className="timeline-entry">
                <div className="timeline-entry-left">
                    <span className="timeline-entry-date">
                        October 2020
                    </span>
                    <span className="timeline-entry-horizontal-line"></span>
                </div>
                <div className="timeline-entry-right">
                    <p className="timeline-entry-para">
                        Landed my first job at TietoEVRY as a Trainee Engineer. Bye Bye college life and hello responsibilities...
                    </p>
                </div>
            </div>
        </div>
    )
}

export default Timeline
